.main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding: 32px;
  overflow: hidden;
  @media #{$mobile} {
    padding: 8px; }

  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media #{$mobile} {
      display: none; } }

  &__content {
    display: flex;
    align-items: center;
    @media #{$mobile} {
      width: 100%;
      height: 100%; }

    &-img {
      margin: auto;

      @media #{$mobile} {
        width: 100%;
        height: 100%;
        object-fit: contain; } }

    .range__wrap {
      @media #{$mobile} {
        display: none; } } }

  &__foot {
    display: flex;
    align-items: center;

    @media #{$mobile} {
      display: none; }

    svg {
      cursor: pointer; } } }
